<template>
  <div>
    <v-app-bar class="hidden-md-and-up" app color="#333333" dark flat>
      <v-btn icon @click.stop="drawer = !drawer">
        <v-icon>mdi-menu</v-icon>
      </v-btn>

      <v-spacer></v-spacer>

      <v-btn icon @click="reproducir()">
        <v-icon>mdi-music</v-icon>
      </v-btn>
    </v-app-bar>

    <v-navigation-drawer class="hidden-md-and-up" v-model="drawer" fixed dark>
      <v-list-item>
        <v-list-item-avatar>
          <v-img
            src="https://gartic.com.br/imgs/mural/on/onepiece_united/luffy-monk.png"
          ></v-img>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title>Carlitos</v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list dense rounded>
        <v-list-item
          v-for="item in items"
          :key="item.text"
          link
          :to="item.link"
        >
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ item.text }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <Reproductor :reproductor="reproductor" />

    <v-app-bar class="hidden-md-and-down" app color="#333333" dark>
      <v-spacer></v-spacer>
      <v-btn
        text
        class="element mx-6"
        v-for="item in items"
        :key="item.text"
        :to="item.link"
      >
        <v-icon left color="primary"> {{ item.icon }} </v-icon>{{ item.text }}
      </v-btn>
      <v-spacer></v-spacer>
      <v-btn icon @click="reproducir()">
        <v-icon>mdi-music</v-icon>
      </v-btn>
    </v-app-bar>
  </div>
</template>

<script>
import Reproductor from "../components/Reproductor.vue";
export default {
  name: "ToolBar",
  data: () => ({
    //
    reproductor: false,

    drawer: false,

    items: [
      { text: "Inicio", link: "/", icon: "mdi-home" },
      {
        text: "Proyectos",
        link: "/proyects",
        icon: "mdi-briefcase-eye-outline",
      },
      { text: "Curriculum", link: "/curriculum", icon: "mdi-file-outline" },
      {
        text: "Error page",
        link: "/noExisto:,)",
        icon: "mdi-alert-octagon-outline",
      },
    ],
  }),
  components: {
    Reproductor,
  },
  methods: {
    reproducir() {
      if (this.reproductor) {
        let miPrimeraPromise = new Promise((resolve) => {
          this.reproductor = false;
          setTimeout(function () {
            resolve(); // ¡Todo salió bien!
          }, 250);
        });

        miPrimeraPromise.then(() => {
          this.reproductor = true;
        });
      } else {
        this.reproductor = true;
      }
    },
  },
};
</script>
<style scoped>
.element {
  text-transform: none;
  font-family: Arial, Helvetica, sans-serif;
  color: white ;
  border-bottom: transparent solid 2px;
  font-size: 1rem;
}

.element:hover {
  cursor: pointer;
  border-bottom: white solid 2px;
  color: #818181 !important;
}

.v-btn--active {
  border-bottom: white solid 2px;
  color: #818181 !important;
}

.theme--dark.v-btn--active:hover::before,
.theme--dark.v-btn--active::before {
  opacity: 0;
}
</style>